@font-face {
  font-family: 'icomoon';
  src:  url('font-icons/icomoon.eot?fbiefc');
  src:  url('font-icons/icomoon.eot?fbiefc#iefix') format('embedded-opentype'),
    url('font-icons/icomoon.woff2?fbiefc') format('woff2'),
    url('font-icons/icomoon.ttf?fbiefc') format('truetype'),
    url('font-icons/icomoon.woff?fbiefc') format('woff'),
    url('font-icons/icomoon.svg?fbiefc#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

